tion.get(\"version\"),\n            description: localization.get(\"description\"),\n            createdBy: localization.get(\"createdBy\"),\n            createDate: localization.get(\"createDate\"),\n            state: \"\",\n        },\n        resizableColumns: false,\n        skin: 'module-versions',\n        texts: {\n            count: \"\",\n            noResults: \"No matching records\",\n\n        },\n    });\n    const dateOptions: any = { month: '2-digit', day: '2-digit', year: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' };\n    onBeforeMount(() => {\n        getVersions();\n    })\n    function getVersions() {\n        executor.execute(async () => {\n            versions.value = await moduleVersionUseCase.listModuleVersions(props.module.ID);\n        }, {loading: true})\n    }\n    function importModuleVersion(moduleVersion: IModuleVersion) {\n        executor.execute(async () => {\n            const applicationID = viewModel.studio.appId;\n            await studio.detachModuleFromApplication(props.module, applicationID, true);\n            props.module.relatedApplications = props.module.relatedApplications.filter(i => i.applicationID != applicationID);\n            await studio.attachModuletoApplication(props.module, applicationID, true, moduleVersion.version);\n            props.module.relatedApplications.push({ applicationID, importedVersion: moduleVersion.version });\n            moduleVersion.relatedApplications!.push({ applicationID, importedVersion: moduleVersion.version });\n            props.module.currentVersion = moduleVersion.version;\n        }, { loading: true })\n        \n    }\n    function computeImportedVersion() {\n        return props.module.currentVersion;\n    }\n</script>\n<template>\n    <div class=\"container vue-table\">\n        <v-client-table ref=\"table\" :data = \"versions\" :columns=\"tableColumns\" :options=\"tableOptions\">\n            <template #createDate=\"props\">\n                    <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dateOptions).format(new Date(props.row.createDate)) }}</span>\n                </template>\n                 <template #state=\"props\">\n                    <button v-if=\"props.row.version == computeImportedVersion()\" class=\"import-btn\" style=\"color: #D5D5D5; cursor: default;\">Using</button>\n                    <button v-else class=\"import-btn\" @click=\"importModuleVersion(props.row)\">Use This Version</button>\n                </template> \n        </v-client-table>\n    </div>\n\n</template>\n<style scoped>\n    @import url(\"../../../../../../src/styles/IDE3.css\");\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .import-btn {\n        background: none;\n        border: none;\n        font-size: 14px;\n        font-weight: 500;\n        color: #2C5A86;\n    }\n</style>"],"sourceRoot":""}]);
